import { GameState } from "othello-rs";
import AgentWorkerMessage, { AgentWorkerReadyMessage, AgentWorkerStartMessage } from "./AgentWorkerMessage";

export { };

function handleMessage(event: MessageEvent) {
  const message: AgentWorkerMessage = event.data;
  switch (message.type) {
    case 'start':
      handleStartMessage(message);
      break;
    case 'cancel':
      // Nothing to do for now.
      break;
    default:
      console.error('Unexpected message type:', message.type);
  }
}

function handleStartMessage(message: AgentWorkerStartMessage) {
  switch (message.settings.type) {
    case 'random': {
      const gameState = GameState.from_js_value(message.gameState);
      const actions = gameState.actions_vec();
      const playerAction = actions[Math.floor(Math.random() * actions.length)];
      const response: AgentWorkerReadyMessage = {
        type: 'ready',
        playerActionId: message.playerActionId,
        playerAction: playerAction.to_js_value(),
      };
      /* eslint-disable-next-line no-restricted-globals */
      self.postMessage(response);
      break;
    }
    default:
      console.error('Unexpected agent type:', message.settings.type);
  }
}

/* eslint-disable-next-line no-restricted-globals */
self.onmessage = handleMessage;
